:root {
  /* Adaptive Design Variables */
  --desktop: 1280px;
  --tablet: 768px;
  --pdesktop: 1279px;
  --ptablet: 767px;
  --responsive-mobile-end: 480px;
  --responsive-mobile-start: 320px;

  /* Transition Variables */
  --default-transition-time: 250ms;
  --default-transition-function: cubic-bezier(0.4, 0, 0.2, 1);

  /* Color Variables */
  /* General */
  --general-background-top-part-color: #f2f5fc;
  --general-background-bot-part-color: #ffffff;
  --general-accent-background-color: #ff751d;
  --general-accent-text-color: #ffffff;
  --general-accent-shadow: drop-shadow(1px 3px 5px rgba(255, 107, 8, 0.35));
  --general-text-color: #52555f;
  --general-header-color: #000000;

  /* Universal Modal */
  --modal-background-color: #ffffff;
  --modal-button-border-color: #f5f6fb;
  --modal-shadow: 10px 10px 30px rgba(82, 85, 95, 0.4);

  /* Header/SharedLayout */
  --header-background-color: #ffffff;
  --header-line-separator-color: #e0e5eb;
  --header-user-background-color: #f5f6fa;

  /* AuthForm */
  --auth-background-top-part-color: #f5f6fb;
  --auth-background-bot-part-color: #ffffff;
  --auth-form-background-color: #ffffff;
  --auth-form-input-background-color: #f6f7fb;
  --auth-form-input-placeholder-color: #a6abb9;
  --auth-form-button-background-color: #f5f6fb;
  --auth-form-button-text-color: #f5f6fb;
  --auth-form-button-shadow: drop-shadow(1px 3px 5px rgba(82, 85, 95, 0.15));
  --auth-form-validation-error-color: #eb5757;

  /* Balance */
  --balance-balance-text-color: #52555fb2;
  --balance-balance-text-opacity: 70%;
  --balance-button-border-color: #ffffff;
  --balance-popup-message-gradient: linear-gradient(
    117.84deg,
    #1d346a 2.84%,
    #031634 67.28%
  );
  --balance-popup-message-text-color: #ffffff;

  /* Transactions */
  /* Navbar */
  --trans-navbar-link-background: #fefefe;
  --trans-navbar-active-link-text-color: #ff751d;
  --trans-background-color: #ffffff;

  /* TransactionForm */
  --trans-form-input-border-color: #f6f7fc;
  --trans-form-input-placeholder-color: #c7ccdc;
  --trans-form-clear-button-border-color: #f6f7fc;
  --trans-dropdown-border-color: #f5f6fb;
  --trans-dropdown-text-color: #c7ccdc;
  --trans-dropdown-background-color: #ffffff;
  --trans-dropdown-active-background-color: #f5f6fb;

  /* TransactionTable */
  --table-head-element-background-color: #f5f6fb;
  --table-element-background-color: #ffffff;
  --table-element-border-color: #f5f6fb;
  --table-income-text-color: #407946;
  --table-expense-text-color: #e7192e;

  /* SummaryTable */
  --summary-background-color: #f5f6fb;
  --summary-border-color: #ffffff;

  /* Report */
  /* Top Panel */
  --top-panel-background-color: #ffffff;
  --top-panel-expense-color: #e53935;
  --top-panel-income-color: #407946;

  /* Diagram */
  --diagram-descended-accent-color: #ffdac0;
}

body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
